body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* Reset */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  border: 0; }

/* Variables */
body {
  font-size: 14px;
  font-family: "Open Sans", sans-serif;
  line-height: 2em; }

h1, h2, h3, h4, h5, h6 {
  font-family: Raleway, sans-serif; }

h1 {
  font-size: 2.3em; }

h2 {
  font-size: 1.728em; }

h3 {
  font-size: 1.44em; }

h4 {
  font-size: 1.2em; }

/* Header */
header {
  display: flex;
  justify-content: space-between;
  padding: 3em; }

header path.logo {
  width: 140.8px;
  -webkit-transform: scale(0.8) translateX(-20px);
          transform: scale(0.8) translateX(-20px); }

header nav {
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  max-width: 250px; }

header nav a, header nav i {
  color: inherit;
  opacity: 0.7;
  text-decoration: none;
  font-size: 1em;
  margin-left: 2em;
  font-family: "Open Sans", sans-serif; }

header nav i:hover {
  cursor: pointer; }

@media (max-width: 700px) {
  header {
    padding: 1em; }
    header .logo path {
      -webkit-transform: scale(0.8);
              transform: scale(0.8); }
    header nav {
      width: 100%;
      min-width: 100px;
      max-width: 217px;
      justify-content: flex-end; }
      header nav a {
        margin-left: 1em; } }

button.get-started {
  width: 300px;
  height: 60px;
  border-radius: 30px;
  font-size: 1em; }

/* Intro */
h1 {
  line-height: 1.5em;
  margin-bottom: 1.5em; }

p {
  margin-bottom: 1em; }

.intro-container {
  background-size: contain;
  background-repeat: no-repeat;
  background-position: bottom; }

.intro {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  max-width: 680px;
  margin: auto;
  text-align: center;
  padding: 0 3em; }
  .intro img {
    width: 100%;
    margin: 5em 0 5em; }
  .intro .get-started {
    margin: 3em 0 5em; }

.intro p {
  max-width: 580px;
  font-size: 1.2em;
  opacity: 0.8; }

/* Feature Grid */
* {
  box-sizing: border-box; }

.feature-grid {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  width: 100%;
  max-width: 900px;
  margin: 0 auto;
  align-self: center;
  padding: 10em 0; }

.feature-grid .feature-square {
  flex-basis: 45%;
  min-width: 350px;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 2em 0; }

.feature-grid .feature-square p {
  width: 100%;
  max-width: 350px;
  text-align: center;
  opacity: 0.8; }

.feature-grid .feature-square .access-img {
  width: 83px;
  height: 78px; }

@media (max-width: 701px) {
  .feature-grid {
    flex-direction: column; }
  .feature-square .img {
    margin-bottom: 3em; } }

/* Image && Text Sections */
.half-img-half-text {
  display: flex;
  padding: 0 8vw 7vh;
  width: 100%;
  justify-content: space-between; }
  .half-img-half-text .section-content {
    flex-basis: 40%;
    display: flex;
    flex-direction: column;
    justify-content: center; }
    .half-img-half-text .section-content h1 {
      margin-bottom: 0.5em;
      width: 100%;
      max-width: 340px; }
    .half-img-half-text .section-content p {
      opacity: 0.8; }
    .half-img-half-text .section-content a {
      text-decoration: none; }
      .half-img-half-text .section-content a .link-decor {
        text-decoration: none;
        padding-bottom: 3px; }
        .half-img-half-text .section-content a .link-decor img {
          height: 0.9em; }
  .half-img-half-text .section-image {
    flex-basis: 50%; }
    .half-img-half-text .section-image img {
      width: 100%;
      max-width: 615px; }

@media (max-width: 750px) {
  .half-img-half-text {
    flex-direction: column; }
    .half-img-half-text .section-content h1 {
      max-width: none; } }

@media (max-width: 700px) {
  .half-img-half-text h1 {
    font-size: 1.5em; }
  .half-img-half-text p {
    font-size: 1.2em; }
  .half-img-half-text .section-image {
    padding-bottom: 4em; } }

/* Testimonials */
.testimonials {
  display: flex;
  width: 100%;
  max-width: 1160px;
  margin: auto;
  justify-content: space-around;
  flex-wrap: wrap;
  height: 600px;
  align-items: center;
  padding: 0 1em; }
  .testimonials .testimonial-square {
    flex-basis: 30%;
    width: 360px;
    min-width: 325px;
    height: 200px;
    padding: 2.5em 2em 1.5em; }
    .testimonials .testimonial-square p {
      width: 100%;
      max-width: 300px;
      opacity: 0.8;
      margin-bottom: 1.5em;
      line-height: 1.7em; }
    .testimonials .testimonial-square .testimonial-profile {
      display: flex;
      height: 25px;
      font-size: 0.6em; }
      .testimonials .testimonial-square .testimonial-profile img {
        height: 100%;
        width: auto;
        border-radius: 50%;
        margin-right: 2em; }
      .testimonials .testimonial-square .testimonial-profile h4 {
        font-size: 1.3em;
        line-height: 1.3em;
        margin-bottom: 0.5em; }
      .testimonials .testimonial-square .testimonial-profile p {
        line-height: 1em;
        margin-bottom: 0; }

.testimonial-quotation {
  position: relative;
  font-size: 15em;
  font-family: sans-serif;
  left: 40px;
  top: 30px; }

@media (max-width: 755px) {
  .testimonials {
    flex-direction: column;
    height: 800px;
    margin-top: 7em; }
    .testimonials .testimonial-square {
      margin-bottom: 2em;
      max-height: 190px; }
    .testimonials .testimonial-quotation {
      font-size: 6em;
      left: -180px;
      top: 100px; } }

/* Call-to-Action */
.call-to-action {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  padding: 10em 3em; }
  .call-to-action .section-content {
    padding: 5em 3em;
    width: 100%;
    max-width: 860px;
    box-shadow: 0px 10px 20px 15px rgba(0, 0, 0, 0.1); }
    .call-to-action .section-content p {
      width: 100%;
      max-width: 620px;
      opacity: 0.8;
      margin: 0 auto 2em; }
  .call-to-action h1 {
    margin-bottom: 2vh; }
  .call-to-action input {
    width: 100%;
    min-width: 200px;
    max-width: 480px;
    height: 50px;
    border-radius: 50px;
    margin-right: 2em;
    margin-bottom: 1em; }
  .call-to-action .get-started {
    width: 200px;
    height: 50px; }

@media (max-width: 700px) {
  .call-to-action .get-started {
    width: 100%;
    max-width: 480px;
    margin-right: 2em; } }

/* Footer */
footer {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  grid-template-rows: repeat(3, 100px);
  padding: 0 0 0 4em;
  font-size: 1em; }
  footer p {
    opacity: 0.8; }
  footer a {
    text-decoration: none;
    color: inherit;
    margin-bottom: 1em;
    opacity: 0.8; }
  footer .footer-header {
    grid-column: 1/6;
    grid-row: 1; }
    footer .footer-header .logo {
      opacity: 1.2;
      float: left; }
  footer .col-1 {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    grid-column: 1/3; }
    footer .col-1 .icon-text {
      display: flex;
      align-items: flex-start; }
      footer .col-1 .icon-text svg {
        margin: 6px 2em 0 0; }
    footer .col-1 p {
      width: 100%;
      max-width: 320px;
      padding-right: 1em; }
  footer .col-2 {
    grid-column: 3; }
    footer .col-2 p {
      display: flex;
      align-items: center; }
    footer .col-2 svg {
      height: 1em;
      margin-right: 1em; }
  footer .col-3 {
    grid-column: 4;
    padding-left: 3em;
    display: flex;
    flex-direction: column; }
  footer .col-4 {
    grid-column: 5;
    padding-left: 2em;
    display: flex;
    flex-direction: column; }
  footer .col-5 {
    grid-column: 6; }
    footer .col-5 .social-icons i {
      margin-right: 0.5em; }

@media (max-width: 830px) {
  .social-icons {
    display: flex;
    flex-direction: column; }
    .social-icons i {
      margin: 0 0.5em 0.5em 1em; } }

@media (max-width: 750px) {
  footer .col-3, footer .col-4 {
    padding-left: 1em; } }

@media (max-width: 700px) {
  footer {
    display: flex;
    flex-direction: column;
    padding-left: 2em;
    font-size: 1.2em;
    text-align: left; }
    footer .footer-header {
      margin-bottom: 3em; }
    footer .col-2 {
      margin-bottom: 4em; }
    footer .col-3 {
      margin-bottom: 2em; }
    footer .col-4 {
      margin-bottom: 4em; }
    footer .social-icons {
      flex-direction: row;
      justify-content: center;
      -webkit-transform: scale(0.8);
              transform: scale(0.8); }
      footer .social-icons i {
        margin: 0; } }



